/* Modal */
.ReactModalPortal > div{
    opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
    align-items: center;
    display: flex;
    justify-content: center;
    transition: opacity .2s ease-in-out;
    z-index: 999;
}

.modal__fondo {
    background-color: rgba(0, 0, 0, 0.3);
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    position: fixed;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
    opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close {
    opacity: 0;
}

.modal {
    background: white;
    border-radius: 5px;
    color: rgb(51, 51, 51);
    display: inline;
    outline: none;
    padding: 10px;
    height: 80vh;
    width: 70vw;
    overflow: scroll;
    display: flex;
    flex-direction: column;
}

.modaltitle{
    text-align: center;
}

.filterbutton{
    margin: 2px;
        &:hover{
            border: 2px solid;
            font-size: 1.1rem;
        
        }
        &:focus{
            background-color: black;
            color: wheat;
        }

}

.saveButtonModal{
    align-self: flex-end;

}

.filterbutton__reset{
    font-size: 1.2rem;
    font-weight: bold;
}